import * as React from 'react';
import * as ReactDOM from 'react-dom';
import App from './App';
import { VisitorsInfoCollector } from 'components/utils/VisitorsInfoCollector';
var root = document.getElementById('deskichat-root');
if (!root) {
    root = document.createElement('div');
    root.id = 'deskichat-root';
    document.body.appendChild(root);
}
VisitorsInfoCollector.init();
ReactDOM.render(React.createElement(App, null), root);



