import { LocalStorage } from 'components/utils/LocalStorage';
export var TicketIdContainer = {
    get: function () {
        return LocalStorage.get('ticketId') || 0;
    },
    set: function (ticketId) {
        LocalStorage.set('ticketId', ticketId);
    },
    clear: function () {
        this.set(0);
    },
};



